<template>
    <section id="calculator" class="calc">
        <div class="container">
            <h2 class="h2">   </h2>
            <div class="calculator">
                <div class="caclulator__column">
                    <p> </p>
                    <v-select v-model="selected" :options="['','', '', ' ', ' ', '', '', '', '', ' ', '']"></v-select>
                </div>
                <div class="caclulator__column">
                    <p>  </p>
                    <v-select v-model="units" :options="['','', '']"></v-select>
                </div>
                <div class="caclulator__column">
                    <p>  ({{ units }})</p>
                    <input v-model="width" placeholder="">
                </div>
                <div class="caclulator__column">
                    <p>  ({{ units }})</p>
                    <input v-model="height" placeholder="">
                </div>
            </div>
            <div class="result">
                <p> : {{ area.toFixed( 1 ) }} {{units}}<sup>2</sup></p>
                <p> : <span>{{ price.toFixed( 1 ) }}</span> </p>
                <button class="btn small-grad-btn" @click="showModal"></button>
            </div>
        </div>

    </section>
</template>

<script>
    import vSelect from 'vue-select'

    export default {
        name: 'calculator',
        components: {
            'v-select': vSelect
        },
        data() {
            return {
                selected: '',
                units: '',
                width : '',
                height : ''
            }
        },
        methods: {
            showModal () {
                this.$modal.show('modal');
            },
            hideModal () {
                this.$modal.hide('modal');
            }
        },
        computed: {
            area: function () {
               return this.width * this.height || 0;
            },
            price: function () {
                let price = 0;
                let rate = 0;
                let koef = 0;
                if (this.units == '') {
                    rate = 1
                } else if (this.units == '') {
                    rate = 10
                } else {
                    rate = 1000
                }

                let currentArea = this.area*rate*rate;

                if (currentArea <= 1000000 && currentArea > 0) {
                    koef = 9000;
                } else
                    if (currentArea > 1000000 && currentArea <= 2000000 && currentArea > 0) {
                        koef = 8000;
                    } else
                    if (currentArea > 2000000 && currentArea <= 5000000 && currentArea > 0) {
                        koef = 6500;
                    } else
                        if (currentArea > 5000000 && currentArea > 0) {
                            koef = 5000;
                        }
                price = koef * currentArea/1000000;
                return price
            }
        }
    }

</script>

